import { useState, useEffect } from "react";
import { getAuth, updateProfile } from "firebase/auth";
import { db } from "../firebase";
import { doc, updateDoc } from "firebase/firestore";
import { useNavigate } from "react-router-dom";


const EditProfile = () => {
  const auth = getAuth();
  const [userInfo, setUserInfo] = useState({
    name: "",
    email: "",
    photoURL: "",
  });
  const [imageFile, setImageFile] = useState(null);  // 
  const [imagePreview, setImagePreview] = useState(null);  // 
  const [uploading, setUploading] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    if (auth.currentUser) {
      setUserInfo({
        name: auth.currentUser.displayName || "",
        email: auth.currentUser.email || "",
        photoURL: auth.currentUser.photoURL || "",
      });
    } else {
      navigate("/"); //  login
    }
  }, [auth, navigate]);

  // 
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result); // 
      };
      reader.readAsDataURL(file); //  Data URL
    }
  };

  // 
  const handleUpdateProfile = async (e) => {
    e.preventDefault();
    try {
      let imageUrl = userInfo.photoURL; //  

      if (imageFile) {
        setUploading(true);

        //  Data URL ( FileReader)
        const reader = new FileReader();
        reader.onloadend = () => {
          imageUrl = reader.result; //  Data URL 
        };
        reader.readAsDataURL(imageFile); //  Data URL
      }

      //  Firebase Authentication
      await updateProfile(auth.currentUser, {
        photoURL: imageUrl,
      });

      //  Firestore
      const userRef = doc(db, "users", auth.currentUser.uid);
      await updateDoc(userRef, {
        name: userInfo.name,
        email: userInfo.email,
        photo: imageUrl,  //  Data URL  Firestore
      });

      alert("!");
      navigate("/dashboard"); //  Dashboard 
    } catch (error) {
      console.error("Error updating profile: ", error);
      alert("!");
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="container mt-5">
      <h1
        style={{
          fontWeight: "bold",
          fontSize: "6rem",
          position: "relative",
          display: "inline-block",
          background: "linear-gradient(90deg, rgba(149, 121, 205, 1) 0%, rgba(0, 0, 0, 1) 100%)",
          WebkitBackgroundClip: "text",
          color: "transparent", //  WebkitTextFillColor  color: transparent
        }}
      >
          EditProfile
      </h1>
      <form onSubmit={handleUpdateProfile}>
        {/*  */}
        <div className="mb-3 text-center">
          
          {imagePreview ? (
            <img
              src={imagePreview}
              alt="Profile Preview"
              className="img-thumbnail w-25 mb-2 rounded-circle"
            />
          ) : (
            <img
              src={userInfo.photoURL || "default-profile-pic.jpg"}
              alt="Current Profile"
              className="img-thumbnail w-25 mb-2 rounded-circle"
            />
          )}
        </div>

        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input type="file" className="form-control" onChange={handleImageChange} />
        </div>

        {/*  */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input
            type="text"
            className="form-control"
            value={userInfo.name}
            onChange={(e) => setUserInfo({ ...userInfo, name: e.target.value })}
          />
        </div>

        {/*  () */}
        <div className="mb-3">
          <label className="form-label"></label>
          <input type="email" className="form-control" value={userInfo.email} disabled />
        </div>

        {/*  */}
        <button
          type="submit"
          disabled={uploading}
          style={{
            backgroundColor: uploading ? "#888888" : "#000000", // 
            color: "#ffffff",
            padding: "16px 16px",
            border: "none",
            borderRadius: "10px",
            cursor: uploading ? "not-allowed" : "pointer", // 
            fontSize: "16px",
            boxShadow: "0 4px 8px rgba(0.1, 0.1, 0.1, 0.3)",
            transition: "background-color 0.3s ease-in-out, color 0.3s ease-in-out",
          }}
          onMouseEnter={(e) => {
            if (!uploading) {
              e.target.style.backgroundColor = "#cdb6fb";
              e.target.style.color = "#000000";
            }
          }}
          onMouseLeave={(e) => {
            if (!uploading) {
              e.target.style.backgroundColor = "#000000";
              e.target.style.color = "#ffffff";
            }
          }}
        >
          {uploading ? "..." : ""}
        </button>
      </form>
    </div>
  );
};

export default EditProfile;