import { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { db } from "../firebase";
import {
  doc,
  getDoc,
  updateDoc,
  collection,
  query,
  getDocs,
  setDoc,
  onSnapshot,
} from "firebase/firestore";
import { QRCodeCanvas } from "qrcode.react";
import { v4 as uuidv4 } from "uuid";
import "bootstrap/dist/css/bootstrap.min.css";

const CheckinPage = () => {
  const { classId } = useParams();
  const navigate = useNavigate();
  const [checkins, setCheckins] = useState([]);
  const [studentsCheckedIn, setStudentsCheckedIn] = useState([]);
  const [latestCheckinNo, setLatestCheckinNo] = useState(null);
  const [studentsScores, setStudentsScores] = useState([]);
  const [showScores, setShowScores] = useState(false);
  const [checkinCode, setCheckinCode] = useState(null); //   Check-in Code
  const [showCheckinCode, setShowCheckinCode] = useState(false); //   state 
  const [studentsMap, setStudentsMap] = useState({}); // ***  state 


  const handleGoToQuestionPage = () => {
    navigate(`/qa/${classId}`);
  };

  const fetchCheckinCode = async () => {
    if (!latestCheckinNo) {
      // alert(" !"); // Removed alert
      return;
    }
    const checkinDoc = await getDoc(
      doc(db, `classroom/${classId}/checkin/${latestCheckinNo}`)
    );
    if (checkinDoc.exists()) {
      setCheckinCode(checkinDoc.data().code);
      setShowCheckinCode(true); //  
    } else {
      setCheckinCode(null);
      // alert(" !"); // Removed alert
    }
  };

  //  ***********************************
  useEffect(() => {
    const fetchStudents = async () => {
      const studentsRef = collection(db, `classroom/${classId}/students`);
      const studentsSnapshot = await getDocs(studentsRef);
      const studentsData = {};
      studentsSnapshot.forEach((doc) => {
        studentsData[doc.id] = { ...doc.data(), id: doc.id }; //  id 
      });
      setStudentsMap(studentsData);
    };

    fetchStudents();
  }, [classId]);

  useEffect(() => {
    if (!latestCheckinNo) return;

    const scoresRef = collection(
      db,
      `classroom/${classId}/checkin/${latestCheckinNo}/scores`
    );
    const unsubscribe = onSnapshot(scoresRef, (querySnapshot) => {
      const scores = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));

      setStudentsScores(scores);
    });

    return () => unsubscribe();
  }, [classId, latestCheckinNo]);

  //  
  const handleSaveScores = async () => {
    try {
      for (let student of studentsScores) {
        const studentRef = doc(
          db,
          `classroom/${classId}/checkin/${latestCheckinNo}/scores/${student.id}`
        );
        await updateDoc(studentRef, {
          score: student.score,
          remark: student.remark,
          status: student.status,
        });
      }
      alert(" !");
    } catch (error) {
      console.error("Error updating scores:", error);
      alert(" !");
    }
  };
  // Add this to your useEffect for loading student scores
  useEffect(() => {
    if (!latestCheckinNo) return;

    const scoresRef = collection(
      db,
      `classroom/${classId}/checkin/${latestCheckinNo}/scores`
    );

    const unsubscribe = onSnapshot(scoresRef, async (querySnapshot) => {
      const scores = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));

      // Fetch answers and combine them with the scores
      const answersData = await fetchAnswers(latestCheckinNo);

      // Merge answers with student scores
      const scoresWithAnswers = scores.map(student => {
        return {
          ...student,
          answers: answersData[student.id] || {}  // Add answers for each student, or empty object if none
        };
      });

      setStudentsScores(scoresWithAnswers);
    });

    return () => unsubscribe();
  }, [classId, latestCheckinNo]);

  // 
  useEffect(() => {
    const q = query(collection(db, `classroom/${classId}/checkin`));
    const unsubscribe = onSnapshot(q, async (querySnapshot) => {
      const checkinList = await Promise.all(
        querySnapshot.docs.map(async (docSnap) => {
          const checkinInfo = docSnap.data();
          const studentQuery = collection(
            db,
            `classroom/${classId}/checkin/${docSnap.id}/students`
          );
          const studentSnapshot = await getDocs(studentQuery);
          return {
            id: docSnap.id,
            ...checkinInfo,
            studentCount: studentSnapshot.size,
          };
        })
      );

      setCheckins(checkinList);
      // Find the latest checkin with status 1 (active)
      const activeCheckin = checkinList.find((checkin) => checkin.status === 1);
      if (activeCheckin) {
        setLatestCheckinNo(parseInt(activeCheckin.id)); // Convert ID to number
      } else {
        setLatestCheckinNo(null);
      }

    });

    return () => unsubscribe();
  }, [classId]);

  // 
  useEffect(() => {
    if (!latestCheckinNo) return;

    const studentCheckinRef = collection(
      db,
      `classroom/${classId}/checkin/${latestCheckinNo}/students`
    );
    const unsubscribe = onSnapshot(studentCheckinRef, (querySnapshot) => {
      const checkedInStudents = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setStudentsCheckedIn(checkedInStudents);
    });

    return () => unsubscribe();
  }, [classId, latestCheckinNo]);

  //  ****
  const handleStartCheckIn = async () => {
    try {
      const newCno = checkins.length + 1;
      const studentsRef = collection(db, `classroom/${classId}/students`);
      const studentsSnapshot = await getDocs(studentsRef);

      //  1.  /classroom/{cid}/checkin/{cno}
      await setDoc(doc(db, `classroom/${classId}/checkin/${newCno}`), {
        code: uuidv4().substring(0, 6).toUpperCase(),
        date: new Date().toLocaleString(),
        status: 1,
      });

      //  2.  /classroom/{cid}/checkin/{cno}/scores
      studentsSnapshot.forEach(async (student) => {
        await setDoc(
          doc(db, `classroom/${classId}/checkin/${newCno}/scores/${student.id}`),
          {
            stdid: student.data().stdid,
            name: student.data().name,
            remark: "",
            score: 0, // 
            status: 0, //  0 = 
            date: "", // 
          }
        );
      });

      alert(`  ${newCno} !`);
      setLatestCheckinNo(newCno);
      //  fetchCheckinCode()   showCheckinCode 
    } catch (error) {
      console.error("Error starting new check-in:", error);
      alert(" !");
    }
  };
  const fetchAnswers = async (checkinId) => {
    const answersCollection = collection(db, `classroom/${classId}/checkin/${checkinId}/answers`);
    const answersSnapshot = await getDocs(answersCollection);

    const answersData = {};
    for (const answerDoc of answersSnapshot.docs) {
      const answerData = answerDoc.data();  // { text: "...", time: "..." }
      //  field  student id
      if (answerData.students) {
        for (const studentId in answerData.students) {
          if (answerData.students.hasOwnProperty(studentId)) {
            const studentAnswer = answerData.students[studentId];

            //  answers 
            if (!answersData[studentId]) {
              answersData[studentId] = {};
            }
            answersData[studentId][answerDoc.id] = studentAnswer; // 
          }
        }
      }
    }
    return answersData;
  };

  // 
  const handleCloseCheckIn = async () => {
    try {
      if (!latestCheckinNo) {
        alert("!");
        return;
      }

      await updateDoc(
        doc(db, `classroom/${classId}/checkin/${latestCheckinNo}`),
        { status: 2 }
      );

      // 
      setCheckinCode(null);
      setShowCheckinCode(false);
      setLatestCheckinNo(null); // Reset latestCheckinNo

      alert("!");
    } catch (error) {
      console.error("Error closing check-in:", error);
      alert("!");
    }
  };

  return (
    <div className="container mt-5">
      {/* <h1
        className="mb-4 text-center"
        style={{
          fontSize: "3rem", // 
          fontWeight: "bold", // 
          color: "#000000", // 
          textTransform: "uppercase", // 
          background: "linear-gradient(90deg, rgba(149, 121, 205, 1) 0%, rgba(0, 0, 0, 1) 100%)", // 
          WebkitBackgroundClip: "text", // 
          color: "transparent", // 
          marginBottom: "30px", // 
        }}
      >
        
      </h1> */}

      <div className="d-flex justify-content-center gap-3 mb-4">
        <button
          onClick={handleStartCheckIn}
          style={{
            backgroundColor: "#000000",
            color: "#ffffff",
            padding: "14px 18px",
            border: "none",
            borderRadius: "10px",
            cursor: "pointer",
            fontSize: "16px",
            fontWeight: "bold",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
            transition: "background-color 0.3s ease-in-out, color 0.3s ease-in-out",
          }}
          onMouseEnter={(e) => {
            e.target.style.backgroundColor = "#cdb6fb";
            e.target.style.color = "#000000";
          }}
          onMouseLeave={(e) => {
            e.target.style.backgroundColor = "#000000";
            e.target.style.color = "#ffffff";
          }}
        >
          
        </button>

        <button
          onClick={handleCloseCheckIn}
          style={{
            backgroundColor: "#ff4d4d",
            color: "#ffffff",
            padding: "14px 18px",
            border: "none",
            borderRadius: "10px",
            cursor: "pointer",
            fontSize: "16px",
            fontWeight: "bold",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
            transition: "background-color 0.3s ease-in-out, color 0.3s ease-in-out",
          }}
          onMouseEnter={(e) => {
            e.target.style.backgroundColor = "#ff4d4d";
            e.target.style.color = "#000000";
          }}
          onMouseLeave={(e) => {
            e.target.style.backgroundColor = "#000000";
            e.target.style.color = "#ffffff";
          }}
        >
          
        </button>
        <button
          onClick={() => {
            if (showCheckinCode) {
              setShowCheckinCode(false);
              setCheckinCode(null); //  
            } else {
              fetchCheckinCode();
            }
          }}
          style={{
            backgroundColor: "#ffcc00",
            color: "#000000",
            padding: "12px 16px",
            border: "none",
            borderRadius: "10px",
            cursor: "pointer",
            fontSize: "14px",
            fontWeight: "bold",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
            transition: "background-color 0.3s ease-in-out, color 0.3s ease-in-out",
          }}
          onMouseEnter={(e) => {
            e.target.style.backgroundColor = "#ffdb4d";
            e.target.style.color = "#000000";
          }}
          onMouseLeave={(e) => {
            e.target.style.backgroundColor = "#000000";
            e.target.style.color = "#ffffff";
          }}
        >
          {showCheckinCode ? "" : " "}
        </button>
      </div>

      {/*   Check-in Code */}
      {checkinCode && (
        <div
          style={{
            backgroundColor: "#cdb6fb",
            padding: "20px",
            borderRadius: "12px",
            textAlign: "center",
            marginBottom: "20px",
            boxShadow: "0 2px 8px rgba(0, 0, 0, 0.2)",
          }}
        >
          <h5 style={{ fontWeight: "bold", marginBottom: "10px" }}></h5>
          <h2
            style={{
              fontSize: "28px",
              fontWeight: "bold",
              color: "#000000",
              backgroundColor: "#ffffff",
              display: "inline-block",
              padding: "10px 20px",
              borderRadius: "8px",
              boxShadow: "0 6px 12px rgba(0, 0, 0, 0.1)",
            }}
          >
            {checkinCode}
          </h2>
        </div>
      )}

      {/*   QR Code */}
      {checkinCode && (
        <div
          style={{
            textAlign: "center",
            padding: "20px",
            backgroundColor: "#ffffff",
            border: "2px solid #cdb6fb",
            borderRadius: "12px",
            marginBottom: "20px",
            boxShadow: "0 6px 12px rgba(0,1, 0.1, 0.1, 0.3)",
          }}
        >
          <h5 style={{ fontWeight: "bold", marginBottom: "10px" }}>QR Code </h5>
          <QRCodeCanvas
            value={`${window.location.origin}/checkin/${classId}/${checkinCode}`}
            size={200}
            style={{
              padding: "10px",
              backgroundColor: "#ffffff",
              borderRadius: "8px",
            }}
          />
        </div>
      )}

      {/*   */}
      <div className="card">
        <div className="card-body">
          <h5></h5>
          {studentsCheckedIn.length > 0 ? (
            <table className="table">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>-</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {studentsCheckedIn
                  .filter((student) => student.date !== "")
                  .map((student, index) => {
                    //  studentsMap  ********************
                    const studentInfo = studentsMap[student.id];
                    return (
                      <tr key={student.id}>
                        <td>{index + 1}</td>
                        {/*  stdid  name  studentsMap */}
                        <td>{studentInfo?.stdid}</td>
                        <td>{studentInfo?.name}</td>
                        <td>{student.date}</td>
                        <td>{student.remark || "-"}</td>
                      </tr>
                    )
                  })}
              </tbody>
            </table>
          ) : (
            <p className="text-muted"></p>
          )}
        </div>
      </div>



      {/*   */}
      <div className="card mt-4">
        <div className="card-body">
          <h5></h5>
          <table className="table">
            <thead>
              <tr>
                <th>#</th>
                <th>-</th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {checkins.map((checkin, index) => (
                <tr key={checkin.id}>
                  <td>{index + 1}</td>
                  <td>{checkin.date}</td>
                  <td>{checkin.studentCount}</td>
                  <td>{checkin.status === 1 ? "" : ""}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/*  / */}
      <button
        className="btn mb-3"
        onClick={() => setShowScores(!showScores)}
        style={{
          backgroundColor: "#000000",
          color: "#ffffff",
          padding: "12px 16px",
          border: "none",
          borderRadius: "10px",
          cursor: "pointer",
          fontSize: "18px",
          fontWeight: "bold",
          marginTop: "20px",
          boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
          transition: "background-color 0.2s ease-in-out, color 0.2s ease-in-out",
        }}
        onMouseEnter={(e) => {
          e.target.style.backgroundColor = "#6d686f";
        }}
        onMouseLeave={(e) => {
          e.target.style.backgroundColor = "#000000";
          e.target.style.color = "#ffffff";
        }}

      >
        {showScores ? "" : ""}
      </button>

     
      {showScores && (
        <div className="card mt-3">
          <div className="card-body">
            <h5></h5>
            {studentsScores.length > 0 ? (
              <table className="table">
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th> {/*  */}
                  </tr>
                </thead>
                <tbody>
                  {studentsScores.map((student, index) => {
                    const studentInfo = studentsMap[student.id];

                    return (
                      <tr key={student.id}>
                        <td>{index + 1}</td>
                        <td>{studentInfo?.stdid}</td>
                        <td>{studentInfo?.name}</td>
                        <td>
                          <input
                            type="text"
                            value={student.remark}
                            onChange={(e) =>
                              setStudentsScores((prev) =>
                                prev.map((s) =>
                                  s.id === student.id
                                    ? { ...s, remark: e.target.value }
                                    : s
                                )
                              )
                            }
                          />
                        </td>
                        <td>{student.date || "-"}</td>
                        <td>
                          <input
                            type="number"
                            value={student.score}
                            onChange={(e) =>
                              setStudentsScores((prev) =>
                                prev.map((s) =>
                                  s.id === student.id
                                    ? { ...s, score: Number(e.target.value) }
                                    : s
                                )
                              )
                            }
                          />
                        </td>
                        <td>{student.status === 1 ? "" : ""}</td>
                          {/*  answers */}
                          
                          <td>
                            {student.answers && Object.keys(student.answers).length > 0 ? (
                              Object.keys(student.answers).map((answerId) => (
                                <div key={answerId}>
                                  {student.answers[answerId]?.text || ""}
                                </div>
                              ))
                            ) : (
                              <span></span>
                            )}
                          </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            ) : (
              <p className="text-muted"></p>
            )}

            {/*   */}
            <button
              className="btn btn-success mt-3"
              onClick={handleSaveScores}
              style={{
                backgroundColor: "#000000", // 
                color: "#ffffff", // 
                padding: "12px 20px", // 
                border: "none", // 
                borderRadius: "8px", // 
                cursor: "pointer", // 
                fontWeight: "bold", // 
                fontSize: "16px", // 
                boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)", // 
                transition: "background-color 0.3s ease-in-out, transform 0.2s ease", // 
              }}
              onMouseEnter={(e) => {
                e.target.style.backgroundColor = "#218838"; //  hover
                e.target.style.transform = "scale(1.05)"; // 
              }}
              onMouseLeave={(e) => {
                e.target.style.backgroundColor = "#000000"; //  hover
                e.target.style.transform = "scale(1)"; // 
              }}
            >
              
            </button>
          </div>
        </div>
      )}

      <div className="text-center mt-4">
        {/*  "" */}
        <button
          className="btn mb-3"
          onClick={() => navigate(`/manage-class/${classId}`)}
          style={{
            padding: "12px 30px",  //  padding 
            fontSize: "16px", // 
            fontWeight: "bold", // 
            borderRadius: "8px",
            backgroundColor: "#000000", // 
            color: "#ffffff", 
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)", // 
            transition: "background-color 0.3s ease-in-out, transform 0.2s ease", // 
          }}
          onMouseEnter={(e) => {
            e.target.style.backgroundColor = "#6c757d"; //  hover
            e.target.style.transform = "scale(1.05)"; // 
          }}
          onMouseLeave={(e) => {
            e.target.style.backgroundColor = "#000000"; // 
            e.target.style.transform = "scale(1)"; // 
            e.target.style.color = "#ffffff";
          }}
        >
          
        </button>

        {/*  "-" */}
        <button
          className="btn mb-3 ms-2"
          onClick={handleGoToQuestionPage}
          style={{
            padding: "12px 30px",  //  padding 
            fontSize: "16px", // 
            fontWeight: "bold", // 
            backgroundColor: "#000000",
            color: "#ffffff",
            borderRadius: "8px", // 
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)", // 
            transition: "background-color 0.3s ease-in-out, transform 0.2s ease", // 
          }}
          onMouseEnter={(e) => {
            e.target.style.backgroundColor = "#007b8f"; //  hover
            e.target.style.transform = "scale(1.05)"; // 

          }}
          onMouseLeave={(e) => {
            e.target.style.backgroundColor = "#000000";
            e.target.style.transform = "scale(1)"; // 
            e.target.style.color = "#ffffff";

          }}
        >
          -
        </button>
      </div>
    </div>
  );
};

export default CheckinPage;